<template>
	<view class="boxStyle">
		<u-action-sheet :title="title || '筛选'" :show="show" :closeOnClickOverlay="true" :closeOnClickAction="true"
			:round="20" :safeAreaInsetBottom='true' @close='close'>
			<!-- <slot></slot> -->
			<u-collapse @change="change" @close="close2" @open="open" :border='false' align='left'>
				<!-- 折叠面板 -->
				<view v-for="item in list" :key="item.title">
					<u-collapse-item :title="item.title">
						<view class="radiobutton_box" v-if='item.type =="btn"'>
							<view @click="btncheckFn(item.code,iitem,item.complex ? true : false)"
								:class="[item.check.indexOf(iitem.check) > -1 ? 'radiobutton radiobuttonC' : 'radiobutton']"
								v-for="iitem in item.children" :key="item.name">
								{{iitem.name}}
							</view>
						</view>


						<u-checkbox-group v-model="checked" placement="column" v-if="item.type == 'checkbox'"
							iconPlacement="right">
							<u-checkbox label="Ethereum"></u-checkbox>
							<u-checkbox label="Polygon"></u-checkbox>
						</u-checkbox-group>

					</u-collapse-item>
					<u-line color="#D4D4D4"></u-line>
				</view>


			</u-collapse>

			<view class="b_btn">
				<u-button :text="buttonName || '确定'" @click='Submit'
					style='background: #0F172C;color: #FFFFFF;font-size: 32rpx;border-radius: 20rpx;height: 115rpx;'>
				</u-button>
			</view>

		</u-action-sheet>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				checked: [],
				list: [{
					title: 'NFT分类', //父级title
					code: 'ntfhj', //分类code
					type: 'btn', //是否为按钮
					check: ['01'], //字符串为单选 --- 数组为多选
					complex: 'complex', //多选标识
					children: [{ //子选项数组
						name: 'IP NFT', //按钮名
						check: '01' //子按钮标识
					}, {
						name: '品牌 NFT',
						check: '02'
					}, {
						name: '个人 NFT',
						check: '03'
					}]
				}, {
					title: '排序',
					code: 'px',
					type: 'btn',
					check: '01',
					children: [{
							name: '最新上市',
							check: '01'
						}, {
							name: '最近创建',
							check: '02'
						}, {
							name: '即将结束',
							check: '03'
						},
						{
							name: '价格从高到低',
							check: '04'
						},
						{
							name: '价格从低到高',
							check: '05'
						}
					]
				}, {
					title: '状态',
					code: 'zt',
					type: 'btn',
					check: ['01'],
					complex: 'complex',
					children: [{
						name: '售卖中',
						check: '01'
					}, {
						name: '有优惠',
						check: '02'
					}, {
						name: '已售罄',
						check: '03'
					}]
				},  {
					title: '类别',
					code: 'lb',
					type: 'btn',
					check: ['01'],
					complex: 'complex',
					children: [{
						name: '艺术',
						check: '01'
					}, {
						name: '收藏品',
						check: '02'
					}, {
						name: '网站域名',
						check: '03'
					}, {
						name: '音乐',
						check: '04'
					}, {
						name: '摄影',
						check: '05'
					}, {
						name: '运动',
						check: '06'
					}, {
						name: '交易卡',
						check: '07'
					}, {
						name: '效用',
						check: '08'
					}, {
						name: '虚拟世界',
						check: '09'
					}]
				}, {
					title: '链条',
					type: 'checkbox' //是否为复选框
				},  ],
			}
		},
		props: ['show', 'title', 'buttonName'],
		//show  是否显示组件  //title 底部弹窗标题  //buttonName 按钮文字 //Submit 提交事件 //closeFn 关闭事件
		methods: {
			Submit() { //提交按钮
				this.$emit('Submit', {
					name: '123'
				});
				this.$emit('closeFn', false);
			},
			close() { //关闭事件
				// this.show = false;
				this.$emit('closeFn', false)
			},
			open(e) {
				// console.log('open', e)
			},
			close2(e) {
				// console.log('close', e)
			},
			change(e) {
				// console.log('change', e)
			},
			btncheckFn(code, row, complex) { //处理多选单选事件
				if (complex) {
					this.list.forEach(item => {
						if (item.code == code && !(item.check && item.check.indexOf(row.check) > -1 ? true :
								false)) {
							console.log(row.check);
							item.check.push(row.check)
						} else if (item.code == code) {
							item.check.forEach((jitem, i) => {
								if (jitem == row.check) {
									item.check.splice(i, 1)
								}
							})
						}
					})
				} else {
					this.list.forEach(item => {
						if (item.code == code) {
							item.check = row.check;
						}
					})
				}

			},
		}
	}
</script>

<style>
	/deep/.u-action-sheet__header__icon-wrap {
		display: none !important;
	}

	/deep/.u-checkbox-label--right[data-v-54acf820] {
		margin-bottom: 38rpx;
	}

	.b_btn {
		width: 100%;
		height: 175rpx;
		background: #FFFFFF;
		opacity: 1;
		border-radius: 0px;
		padding: 30rpx 40rpx;
		box-sizing: border-box;
	}

	.u-line[data-v-60c80200] {
		width: 670rpx !important;
		margin: 0 auto !important;
		opacity: 0.6 !important;
	}

	.radiobutton_box {
		display: flex;
		flex-wrap: wrap;
	}

	.radiobutton {
		min-width: 180rpx;
		height: 72rpx;
		background: #FFFFFF;
		border: 2rpx solid #0F172C;
		opacity: 1;
		border-radius: 20rpx;
		font-size: 28rpx;
		font-family: Source Han Sans CN;
		font-weight: 400;
		color: #333333;
		opacity: 1;
		line-height: 72rpx;
		text-align: center;
		margin-right: 32rpx;
		margin-bottom: 20rpx;
		padding: 0 28rpx;
		box-sizing: border-box;
	}

	.radiobuttonC {
		background: #0F172C;
		color: #ffffff;
	}
</style>
